#test-main-container {
    text-align: center;
    flex:1;
    flex-direction: column;
    align-items: center;
    /* background-image: url('../assets/page_test-1.jpg'); */
	background-repeat: round;
    max-width: 400px;
    margin: 0 auto;
}

#test-main-container #header-bar {
	background-color: #8f1ba4;
	height: 24px;
	border: none;
	margin: 0;
}

#test-main-container #progress-text-container {
    font-weight: 500;
    font-size: 22px;
    color: #000;
    text-align: left;
    margin: 13px 0 10px 22px;
}

#test-main-container .description {
    text-align: center;
    font-size: 21px;
    font-weight: 700;
    line-height: 1.6;
    padding: 0 30px;
    margin-top: 40px;
    white-space: pre-line;
}

#test-main-container .button-container {
    margin-top: 32px;
    text-align: center;
}

#test-main-container .test-button {
    width: 76%;
    max-width: 315px;
    color: #FFFFFF;
    background-color: #000000;
    height: 70px;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.6;
    align-items: center;
    border-radius: 15px;
    margin-bottom: 22px;
    cursor: pointer;
    border: none;
    outline: none;
    white-space: pre-line;
    box-shadow: 5px 5px 4px #878787;
}

#test-main-container .image {
    width: 85%;
    margin-top: 20px;
}