@font-face {
	font-family: 'NanumBarunGothic';
	font-style: normal;
	font-weight: 400;
	src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.eot');
	src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.eot?#iefix') format('embedded-opentype'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.woff') format('woff'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.ttf') format('truetype');
}

@font-face {
	font-family: 'NanumBarunGothic';
	font-style: normal;
	font-weight: 700;
	src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebBold.eot');
	src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebBold.eot?#iefix') format('embedded-opentype'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebBold.woff') format('woff'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebBold.ttf') format('truetype')
}

@font-face {
	font-family: 'NanumBarunGothic';
	font-style: normal;
	font-weight: 300;
	src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebLight.eot');
	src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebLight.eot?#iefix') format('embedded-opentype'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebLight.woff') format('woff'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWebLight.ttf') format('truetype');
}

html {
	height: 100%;
}
body {
	height: 100%;
	user-select: none;
	margin: 0px;
}
body * {
	font-family: 'NanumBarunGothic', sans-serif;
}
#root {
	text-align: center;
	min-height: 100%;
	position: relative;    
}

#home-main-container {
	text-align: center;
	/* background-image: url('../assets/page_main-1.jpg'); */
	background-repeat: round;
	max-width: 400px;
	margin: 0 auto;
	display: flex;
    flex-direction: column;
}

#home-main-container #header-bar {
	background-color: #000000;
	height: 24px;
	border: none;
	margin: 0;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

#home-main-container #content-container {
    margin-top: 40px;
    text-align: center;
}


#home-main-container #main-text {
	margin-top: 90px;
    font-size: 24px;
    font-weight: 600;
    color: #000000
}

#home-main-container #sub-text {
    margin-top: 38px;
    font-size: 18px;
	font-weight: 600;
	color: #000000;
	line-height: 24px;
}

#home-main-container #button-container {
    margin-top: 50px;
}

#home-main-container #btn-start-test {
    width: 235px;
    height: 72px;
    cursor: pointer;
    border: none;
	outline: none;
	background: url('../assets/start.png');
	background-repeat: round;
}

#home-main-container #logo-container {
	margin: 60px 0;
}

#home-main-container #logo {
    width: 115px;
	height: 80px;
}

#home-main-container #footer-bar {
	background-color: #8f1ba4;
	height: 24px;
	border: none;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0;
}

/* 575 이상 넘어가는 사이즈 */
@media screen and (min-width: 575px) {
	#home-main-container #main-text {
		font-size: 38px;
	}

	#home-main-container #sub-text {
		margin-top: 27px;
		font-size: 24px;
		line-height: 29px;
	}

	#home-main-container #btn-start-test {
		width: 360px;
		height: 100px;
	}

	#home-main-container #logo {
		width: 130px;
		height: 100px;
	}
}
